import { useEffect, useState} from "react";
import { Link } from 'react-router-dom'
import './Welcome.css'
export default function Welcome(props) {
  const [position, setPosition] = useState(0);
  function onScroll() {
    setPosition(window.scrollY);
  }
  useEffect(() => {
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("scroll", onScroll);
    };
  }, []);

  return (
    <div className="wrapper">
      <div
        className="bg bg1"
        style={{
          backgroundPositionY: position / 2,
        }}
      >
        <div>2023   </div>
      </div>

      <div className="bg bg3" style={{
          transform: `translateX(${position}px)`,
        }}/>
      <p
        className="desc3"
        style={{
          opacity: (position - 700) / 50,
        }}
      >
        10   
      </p>
      <div className="bg bg4" style={{
          transform: `translateX(-${position-760}px)`,
        }}/>  

      <div className="bg bg5" style={{
          backgroundPositionY: -position / 2,
        }}>
          <div className="bgDiv2">
          <Link to={'/invitation'} className="btnLink">
            <button class="btn-1"> </button>
          </Link>
          </div>
        </div>

      <p
        className="desc3"
        style={{
          opacity: (position - 700) / 50,
        }}
      >
        23 .
      </p>
    </div>
  );
}